.login-container {
  padding: 2rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login-container .btn{
  margin: 16px 0 16px 0;
}
  
.login-subscription {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 24px 0 24px 0;
  padding: 24px;
  color: var(--primary);
}
  
.login-subscription > p {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}
  
.login-subscription-heading {
  margin: 24px 0 24px 0;
  font-size: 24px;
}
  
.login-input {
  padding: 8px 24px;
  border-radius: 2px;
  margin: 16px 0 16px 0px;
  outline: none;
  border: none;
  font-size: 18px;
  border: 1px solid var(--primary);
}

.login-input::placeholder {
  color: #b1b1b1;
}
  
@media screen and (max-width: 820px) {
  
  .login-input {
    width: 100%;
  }

  .btn {
    width: 100%;
  }
}

.errors{
  color: red;
  font-size: x-small;
}
